@import "./default/default.scss";
.products-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(300px, 410px));
  justify-content: center;
  align-items: center;
  row-gap: 50px;
  column-gap: 30px;
  @media (max-width: 1150px) {
    grid-template-columns: repeat(2, minmax(300px, 410px));
  }
  @media (max-width: 800px) {
    grid-template-columns: repeat(1, minmax(300px, 610px));
  }
  @media (max-width: $mobile) {
    row-gap: 10px;
  }
  .product-item {
    width: 100%;
    position: relative;
    &.promotion {
      &::before {
        color: #dd133b;
      }
      &::after {
        color: $white;
        content: "Акция";
      }
    }
    &.novelty {
      &::before {
        color: #e1843e;
      }
      &::after {
        color: $white;
        content: "Новинка";
      }
    }
    &.promotion,
    &.novelty {
      &::after,
      &::before {
        top: 20px;
        left: -2px;
      }
      &:before {
        position: absolute;
        font-family: icomoon;
        font-size: 33px;
        line-height: 33px;
        content: "\e926";
        z-index: 2;
      }
      &::after {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 80px;
        height: 31px;
        @include rale-800-8;
        line-height: 33px;
        z-index: 2;
      }
    }
    border: 1px solid rgba(0, 0, 0, 0.08);
    &__swiper {
      position: relative;
      overflow: hidden;
    }
    &__name {
      margin-top: 17px;
      @include rale-700-18;
      padding-left: 21px;
      padding-right: 62px;
    }
    &__code {
      @include mont-500-15;
      padding-left: 22px;
      color: $gray;
    }
    &__type {
      @include rale-600-16;
      margin-top: 9px;
      padding-left: 22px;
      padding-right: 62px;
      line-height: 20px;
    }
    &__additional-information {
      display: flex;
      justify-content: space-between;
      margin-top: 17px;
    }
    &__footer {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-top: 1px solid rgba(0, 0, 0, 0.08);
      padding: 14px 22px 12px 22px;
    }
    .swiper-wrapper.product-item__wrapper {
      width: 100%;
      height: 100%;
      .swiper-slide {
        height: 278px;
        width: 100%;
        background-color: #fcfcfc;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
          max-height: 235px;
          max-width: 243px;
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
        &:hover {
          &::before {
            margin-top: 14px;
            content: "";
            position: absolute;
            width: 55px;
            height: 55px;
            background: rgba(255, 255, 255, 0.15);
            border-radius: 50%;
            cursor: pointer;
          }
          &::after {
            margin-top: 14px;
            position: absolute;
            content: "\e903";
            font-family: icomoon;
            font-size: 19px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 45px;
            height: 45px;
            background-color: $green;
            border-radius: 50%;
            color: $white;
            cursor: pointer;
          }
        }
      }
    }
    .additions-information__addition-block {
      padding-right: 30px;
      padding-left: 22px;
      padding-bottom: 20px;
      display: flex;
      align-items: center;
      gap: 10px;
      .addition-block__icon {
        color: $darkGreen;
        font-size: 18px;
        margin-bottom: 2px;
      }
      .icon-balance {
      }
      .icon-bag-2 {
        margin-right: 2px;
        margin-bottom: 5px;
      }
      .addition-block-info {
        display: flex;
        flex-direction: column;
        gap: 0px;
        @include mont-600-14;
        line-height: 16px;
        &__type {
          color: $gray;
        }
        &__value {
          color: $darkGreen;
        }
      }
    }
    .product-item__price-block {
      display: flex;
      align-items: baseline;
      .product-item__price-text {
        @include mont-600-15;
        line-height: 18px;
        color: $darkBlue;
      }
      .product-item__price-value {
        padding-bottom: 2px;
        @include mont-600-20;
        color: $darkGreen;
        margin-left: 7px;
        line-height: 24px;
        .product-item__price-valute {
          @include mont-600-15;
          margin-left: 3px;
        }
      }
      .product-item__price-discount {
        position: relative;
        @include mont-500-16;
        color: $gray;
        line-height: 20px;
        margin-left: 11px;
        .product-item__price-valute {
          @include mont-500-13;
          margin-left: 3px;
        }
        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: 55%;
          width: 100%;
          height: 1px;
          background-color: $gray;
        }
      }
    }
    .rectangle.background {
      padding: 11.8px 36px 11.8px 36px;
      @media (max-width: $mobile) {
        padding: 10px 15px;
      }
      &:hover {
      }
      &:active {
      }
      &:focus {
      }
    }
    &:hover {
      border: 1.5px solid #93b474;
      .swiper-button-prev {
        color: $darkGreen;

        margin-top: 1px;
        margin-left: 6px;
        width: 23px;
        height: 23px;
        &::after {
          content: "\e919";
          font-family: icomoon;
          font-size: 23px;
          transform: rotate(180deg);
        }
      }
      .swiper-button-next {
        margin-top: 1px;
        margin-right: 6px;
        color: $darkGreen;
        width: 23px;
        height: 23px;
        &::after {
          content: "\e919";
          font-family: icomoon;
          font-size: 23px;
        }
      }
      .swiper-button-disabled {
        color: #c4c4c4;
      }
    }
  }
}
