@charset "UTF-8";
.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.products-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(300px, 410px));
  justify-content: center;
  align-items: center;
  row-gap: 50px;
  column-gap: 30px;
}
@media (max-width: 1150px) {
  .products-list {
    grid-template-columns: repeat(2, minmax(300px, 410px));
  }
}
@media (max-width: 800px) {
  .products-list {
    grid-template-columns: repeat(1, minmax(300px, 610px));
  }
}
@media (max-width: 600px) {
  .products-list {
    row-gap: 10px;
  }
}
.products-list .product-item {
  width: 100%;
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.08);
}
.products-list .product-item.promotion::before {
  color: #dd133b;
}
.products-list .product-item.promotion::after {
  color: #ffffff;
  content: "Акция";
}
.products-list .product-item.novelty::before {
  color: #e1843e;
}
.products-list .product-item.novelty::after {
  color: #ffffff;
  content: "Новинка";
}
.products-list .product-item.promotion::after, .products-list .product-item.promotion::before, .products-list .product-item.novelty::after, .products-list .product-item.novelty::before {
  top: 20px;
  left: -2px;
}
.products-list .product-item.promotion:before, .products-list .product-item.novelty:before {
  position: absolute;
  font-family: icomoon;
  font-size: 33px;
  line-height: 33px;
  content: "\e926";
  z-index: 2;
}
.products-list .product-item.promotion::after, .products-list .product-item.novelty::after {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 31px;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-size: 8px;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  line-height: 33px;
  z-index: 2;
}
.products-list .product-item__swiper {
  position: relative;
  overflow: hidden;
}
.products-list .product-item__name {
  margin-top: 17px;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  letter-spacing: 0.01em;
  padding-left: 21px;
  padding-right: 62px;
}
.products-list .product-item__code {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  letter-spacing: 0.01em;
  padding-left: 22px;
  color: #595959;
}
.products-list .product-item__type {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 0.01em;
  margin-top: 9px;
  padding-left: 22px;
  padding-right: 62px;
  line-height: 20px;
}
.products-list .product-item__additional-information {
  display: flex;
  justify-content: space-between;
  margin-top: 17px;
}
.products-list .product-item__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.08);
  padding: 14px 22px 12px 22px;
}
.products-list .product-item .swiper-wrapper.product-item__wrapper {
  width: 100%;
  height: 100%;
}
.products-list .product-item .swiper-wrapper.product-item__wrapper .swiper-slide {
  height: 278px;
  width: 100%;
  background-color: #fcfcfc;
  display: flex;
  justify-content: center;
  align-items: center;
}
.products-list .product-item .swiper-wrapper.product-item__wrapper .swiper-slide img {
  max-height: 235px;
  max-width: 243px;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.products-list .product-item .swiper-wrapper.product-item__wrapper .swiper-slide:hover::before {
  margin-top: 14px;
  content: "";
  position: absolute;
  width: 55px;
  height: 55px;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 50%;
  cursor: pointer;
}
.products-list .product-item .swiper-wrapper.product-item__wrapper .swiper-slide:hover::after {
  margin-top: 14px;
  position: absolute;
  content: "\e903";
  font-family: icomoon;
  font-size: 19px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  background-color: #93b474;
  border-radius: 50%;
  color: #ffffff;
  cursor: pointer;
}
.products-list .product-item .additions-information__addition-block {
  padding-right: 30px;
  padding-left: 22px;
  padding-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}
.products-list .product-item .additions-information__addition-block .addition-block__icon {
  color: #337d5a;
  font-size: 18px;
  margin-bottom: 2px;
}
.products-list .product-item .additions-information__addition-block .icon-bag-2 {
  margin-right: 2px;
  margin-bottom: 5px;
}
.products-list .product-item .additions-information__addition-block .addition-block-info {
  display: flex;
  flex-direction: column;
  gap: 0px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.01em;
  line-height: 16px;
}
.products-list .product-item .additions-information__addition-block .addition-block-info__type {
  color: #595959;
}
.products-list .product-item .additions-information__addition-block .addition-block-info__value {
  color: #337d5a;
}
.products-list .product-item .product-item__price-block {
  display: flex;
  align-items: baseline;
}
.products-list .product-item .product-item__price-block .product-item__price-text {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  letter-spacing: 0.01em;
  line-height: 18px;
  color: #1a2f3f;
}
.products-list .product-item .product-item__price-block .product-item__price-value {
  padding-bottom: 2px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  letter-spacing: 0.01em;
  color: #337d5a;
  margin-left: 7px;
  line-height: 24px;
}
.products-list .product-item .product-item__price-block .product-item__price-value .product-item__price-valute {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  letter-spacing: 0.01em;
  margin-left: 3px;
}
.products-list .product-item .product-item__price-block .product-item__price-discount {
  position: relative;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 0.01em;
  color: #595959;
  line-height: 20px;
  margin-left: 11px;
}
.products-list .product-item .product-item__price-block .product-item__price-discount .product-item__price-valute {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  letter-spacing: 0.01em;
  margin-left: 3px;
}
.products-list .product-item .product-item__price-block .product-item__price-discount::after {
  content: "";
  position: absolute;
  left: 0;
  top: 55%;
  width: 100%;
  height: 1px;
  background-color: #595959;
}
.products-list .product-item .rectangle.background {
  padding: 11.8px 36px 11.8px 36px;
}
@media (max-width: 600px) {
  .products-list .product-item .rectangle.background {
    padding: 10px 15px;
  }
}
.products-list .product-item:hover {
  border: 1.5px solid #93b474;
}
.products-list .product-item:hover .swiper-button-prev {
  color: #337d5a;
  margin-top: 1px;
  margin-left: 6px;
  width: 23px;
  height: 23px;
}
.products-list .product-item:hover .swiper-button-prev::after {
  content: "\e919";
  font-family: icomoon;
  font-size: 23px;
  transform: rotate(180deg);
}
.products-list .product-item:hover .swiper-button-next {
  margin-top: 1px;
  margin-right: 6px;
  color: #337d5a;
  width: 23px;
  height: 23px;
}
.products-list .product-item:hover .swiper-button-next::after {
  content: "\e919";
  font-family: icomoon;
  font-size: 23px;
}
.products-list .product-item:hover .swiper-button-disabled {
  color: #c4c4c4;
}